/* Common settings */
*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    color: #444;
    font-family: Montserrat, 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 14px
}
.page {
    width: 100%;
    position: relative;
    background: #fff;
}
.container {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 20px;
}
.title {
    margin-bottom: 20px;

    font-family: Monserrat, 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 24px;
    font-weight: bold;
    text-transform: uppercase;
}
.text {
    font-family: Muli, 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 14px;
    font-weight: normal;
    line-height: 24px;
}
/* End of common settings */

/* Intro styles */
.intro {
    width: 100%;
    padding-bottom: 190px;

    color: #fff;

    background:
        url(../../images/intro/intro-bg.jpg) no-repeat,
        #313131;
    background-position: center;
    background-size: cover;
}
.header {
    display: flex;
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
    justify-content: space-between;
    align-items: baseline;

    padding: 40px 100px;
}
.logo {
    max-width: 300px;
    overflow: hidden;
}
.nav__inner {
    margin: 0;
    list-style: none;
}
.nav__item {
    display: inline-block;
    margin-left: 40px;
}
.nav__link {
    position: relative;

    font-family: Muli, 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: #f0f0f0;
    text-decoration: none;
    text-transform: uppercase;

    transition: all 0.2s ease;
}
.nav__link:hover {
    color: #fff;
}
.nav__link::after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    position: absolute;
    left: 0;
    bottom: -2px;

    background: #fff;
    opacity: 0;
    transition: all 0.2s ease;
}
.nav__link:hover::after {
    opacity: 1;
}
.intro__content {
    margin-top: 150px;
    padding-left: 38%;
    padding-right: 35px;
}
.slider {
    position: relative;
}
.slider__box {
    max-width: 680px;
}
.slider__item {
    max-width: 680px;
    float: left;
    z-index: 0;
}
.slide__sup-title {
    font-family: 'Playfair Display', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 30px;
    font-weight: normal;
}
.slide__title {
    margin: 25px 0;
    font-size: 55px;
    line-height: 70px;
}
.slider__dots {
    position: absolute;
    right: -90px;
    top: 50%;
    transform: translate(-65px, -50%);
}
.slider__dots .transparent-circle {
    display: block;
    width: 8px;
    height: 8px;
    margin-bottom: 10px;

    border-radius: 4px;
    background: #313131;
}
.slider__dots .transparent-circle.slick-active {
    background: #ffffff;
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
}
.slider__dots li {
    cursor: pointer;
}
.slick-dotted.slick-slider {
    margin: 0;
}
.link {
    font-family: Muli, 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 14px;
    text-decoration: none;
    color: #757575;
}
.link_type_button {
    display: inline-block;
    margin: 35px 0;
    padding: 12px 40px;
    position: relative;

    font-weight: bold;

    border-radius: 44px;
    background: #fff;

    transition: all 0.2s ease;
}
.link_theme_arrow {
    padding: 12px 65px 12px 40px;
    border: 2px solid #fff;
}
.link_theme_arrow::after {
    content: '';
    display: block;
    width: 15px;
    height: 15px;
    position: absolute;
    left: 127px;
    top: 13px;

    transition: all 0.2s ease;
    background: url(../../images/_common/icons/icons-arrow-right.png) no-repeat;
    background-size: cover;
}
.link_theme_arrow:hover::after {
    left: 132px;
}
/* End intro styles */

/* Services styles */
.services {
    width: 100%;
    height: 500px;

    background: url(../../images/services/services-bg-3.png) no-repeat;
    background-position: right 0px top 0px;
    background-size: 200px 500px;
}
.services__inner {
    display: flex;
    width: 100%;
    max-width: 1140px;

    margin: 0 auto;
    position: absolute;
    top: 860px;
    left: 0;
    right: 0;

    box-shadow: 0 5px 30px rgba(0,0,0,0.2);
    overflow: hidden;
}
.services__header {
    width: 360px;
    padding: 100px 40px;
    position: relative;

    color: #fff;

    background:
        url(../../images/services/services-bg-2.png) no-repeat,
        url(../../images/services/services-bg-1.jpg) no-repeat;
    background-position: right -20px bottom -30px, center;
    background-size: 185px 175px, cover;
}
.services__link {
    position: absolute;
    left: -20px;
    bottom: 10px;
}
.link_theme_white {
    border: 2px solid #fff;
}
.link_theme_white:hover {
    color: #fff;
    background-color: #009688;
}
.services__categories {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: calc(100% - 360px);
    padding: 100px 40px 140px;

    background: #fff;
}
.categories__item {
    margin-bottom: 35px;
    position: relative;
}
.categories__ico {
    width: 35px;
    height: 35px;
    position: absolute;
    left: 0;
    top: 5px;
    
    object-fit: contain;
}
.categories__text {
    width: 100%;
    max-width: 255px;
    margin-left: 50px;
}
.categories__title {
    font-size: 18px;
}
.categories__desc {
    margin-top: 20px;
}
/* End services styles */

/* Company value styles */
.company-value {
    width: 100%;

    background: url(../../images/company-value/company-value-bg.png) no-repeat;
    background-position: right bottom;
    background-size: 680px 610px;
}
.company-value__inner {
    padding: 0 35px 100px 35%;
    position: relative;
}
.company-value__bg {
    width: 475px;
    height: 580px;
    position: absolute;
    left: -120px;
    bottom: 0;

    background: url(../../images/company-value/company-value-man.png) no-repeat;
    background-position: right bottom;
    background-size: 475px 580px;
}
.company-value__content {
    z-index: 100;
}
.company-value__desc {
    margin-bottom: 60px;
}
.graph {
    width: 726px;
    height: 355px;
    position: relative;
}
.graph__img {
    display: block;
    width: 300px;
    height: 277px;
    position: absolute;
    left: 215px;
    top: 0;
}
.graph__main-caption {
    position: absolute;
    left: 320px;
    top: 55px;
    z-index: 1;
}
.graph__title {
    font-size: 24px;
    color: #009688;
}
.graph__caption {
    display: block;
    width: 190px;
    position: absolute;
}
.graph__caption_first {
    left: 0;
    top: 60px;
}
.graph__caption_second {
    left: 107px;
    top: 257px;
}
.graph__caption_third {
    right: 0;
    top: 152px;
}
.graph__value {
    position: absolute;
    left: 135px;
    top: -55px;

    font-size: 30px;
    color: #009688;
}
.graph__dot {
    width: 15px;
    height: 15px;
    position: absolute;

    border: 3px solid #009688;
    border-radius: 50%;

    background-color: #00bca9;
}
.graph__dot::after {
    content: '';
    display: block;
    width: 107px;
    height: 2px;
    position: absolute;
    left: 12px;
    top: 4px;

    background-color: #00bca9;
}
.graph__dot_first {
    left: 110px;
    top: -25px;
}
.graph__dot_second {
    left: 50px;
    top: -25px;
}
.graph__dot_third {
    left: 76px;
    top: -25px;
}
.graph__dot_third::after {
    width: 100px;
    left: -103px;
}
/* End company-value styles */

/* Banner styles */
.banner {
    width: 100%;
    color: #fff;
    background: url(../../images/banner/banner-bg.jpg) no-repeat;
    background-position: center;
    background-size: cover;
}
.banner__inner {
    width: 100%;
    padding: 80px 0;
}
.banner__title {
    max-width: 890px;
    margin: 20px auto;
    text-align: center;
}
.banner__desc {
    max-width: 760px;
    margin: 0 auto;
    text-align: center;
}
.banner__buttons {
    text-align: center;
}
.banner__button:first-child {
    margin-right: 30px;
}
.link_theme_green {
    color: #fff;
    border: 2px solid #fff;
    background-color: #009688;
}
.link_theme_green:hover {
    color: #444;
    border: 2px solid #fff;
    background-color: #fff;
}
/* End banner styles */

/* Core-services styles */
.core-services {
    width: 100%;
}
.core-services__inner {
    width: 100%;
    padding: 80px 0;
}
.core-services__title {
    max-width: 890px;
    margin: 20px auto;
    text-align: center;
}
.core-services__desc {
    max-width: 760px;
    margin: 0 auto 40px;
    text-align: center;
}
.core-services__categories {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
/* End core-services styles */

/* Images-box styles */
.images-box {
    width: 100%;
    background: url(../../images/core-services/core-services-bg.png) no-repeat, #fff;
    background-position: right bottom;
    background-size: 636px 636px;
}
.images-box__inner {
    width: 100%;
    padding: 80px 0;
}
.images-box__title {
    max-width: 890px;
    margin: 20px auto;
    text-align: center;
}
.images-box__desc {
    max-width: 760px;
    margin: 0 auto 40px;
    text-align: center;
}
.cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.cards__item {
    width: 350px;
    min-height: 330px;
    position: relative;
}
.cards__img {
    display: block;
    width: 350px;
    height: 230px;
}
.cards__text {
    width: 310px;
    margin: -80px auto 20px;
    padding: 18px;
    position: relative;

    border-top: 1px solid #e9e9e9;
    background: linear-gradient(to bottom, #fff 50%, transparent);
}
.cards__text::before {
    content: '';
    display: block;
    width: 1px;
    height: 100%;
    position: absolute;
    left: -1px;
    top: -1px;

    background: linear-gradient(to top, transparent, #e9e9e9 50%, #e9e9e9);
}
.cards__text::after {
    content: '';
    display: block;
    width: 1px;
    height: 100%;
    position: absolute;
    right: -1px;
    top: -1px;

    background: linear-gradient(to top, transparent, #e9e9e9 50%, #e9e9e9);
}
.cards__title {
    margin-bottom: 10px;
    font-size: 18px;
}
.cards__sub-title {
    font-size: 14px;
}
.cards__desc {
    margin-top: 25px;
}
/* End images-box styles */

/* Twitter-feed styles */
.twitter-feed {
    width: 100%;
    background-color: #009688;
}
.twitter-feed__inner {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding: 45px 0;
}
.twitter-feed__text {
    color: #fff;
}
.twitter-feed__link {
    display: block;
    width: 65px;
    height: 65px;
    position: relative;

    border-radius: 50%;
    background: url(../../images/_common/icons/icons-twitter.svg) no-repeat;
    background-position: center;
    background-size: 35px 35px;

    text-indent: -10000px;
    overflow: hidden;
}
/* End twitter-feed styles */

/* Footer styles */
.footer {
    width: 100%;

    color: #f1f1f1;
    font-size: 13px;
    line-height: 20px;

    background-color: #313131;
}
.footer__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    padding: 80px 0 60px;
}
.footer__element {
    margin-bottom: 30px;
}
.recent-news {
    width: 100%;
    width: 260px;
}
.recent-news__title {
    margin-bottom: 40px;
    font-size: 16px;
}
.recent-news__item {
    display: block;
    width: 100%;
    height: 66px;
    margin-bottom: 50px;
    position: relative;

    color: #fff;
    text-decoration: none;
}
.recent-news__img {
    width: 63px;
    height: 63px;
    position: absolute;
    left: 0;
    top: 3px;

    object-fit: cover;
}
.recent-news__text {
    width: calc(100% - 80px);
    margin-left: 80px;
}
.recent-news__desc {
    margin: 0;

    color: #f1f1f1;
    font-size: 13px;
    line-height: 20px;
    text-decoration: none;
    transition: all 0.2s ease;
}
.recent-news__item:hover .recent-news__desc {
    text-decoration: underline;
}
.recent-news__data {
    margin: 0;

    color: #5f5f5f;
    font-size: 12px;
    line-height: 20px;
}
.useful-links {
    width: 100%;
    width: 260px;
}
.useful-links__title {
    margin-bottom: 40px;
    font-size: 16px;
}
.useful-links__list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 260px;
    margin: 0;
}
.useful-links__item {
    display: block;
    width: 50%;
    margin-bottom: 18px;

    list-style: none;
}
.useful-links__link {
    color: #828181;
    text-decoration: none;
    transition: all 0.2s ease;
}
.useful-links__link:hover {
    color: #f1f1f1;
}
.mailing-list {
    width: 260px;
    color: #f1f1f1;
}
.mailing-list__title {
    margin-bottom: 40px;
    font-size: 16px;
}
.mailing-list__form {
    margin-bottom: 50px;
}
.mailing-list__form-desc {
    color: #828181;
    line-height: 20px;
}
.mailing-list__form-sub-desc {
    color: #5f5f5f;
    line-height: 20px;
}
.mailing-list__email {
    width: 213px;
    height: 34px;
    margin: 14px 5px 14px 0;
    padding: 0 20px;

    background: #f5f5f5;
    border: none;
    outline: none;
    transition: all 0.2s ease;
}
.mailing-list__email:hover,
.mailing-list__email:focus {
    box-shadow: inset 0 0 5px rgba(0,0,0,0.6);
}
.mailing-list__submit {
    width: 38px;
    height: 34px;
    margin: 14px 0;

    text-indent: -1000px;
    border: none;
    outline: none;

    background: url(../../images/_common/icons/icons-check-mark.svg) no-repeat, #009688;
    background-position: center center;
    background-size: 16px 16px;

    overflow: hidden;
    cursor: pointer;
    transition: all 0.2s ease;
}
.mailing-list__submit:hover,
.mailing-list__submit:active {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.6);
}
.social-box__item {
    display: inline-block;
    width: 36px;
    height: 33px;
    margin-right: 5px;
    position: relative;

    background: #2e2e2e;

    text-indent: -1000px;
    overflow: hidden;
    transition: all 0.2s ease;
}
.social-box__item:last-child {
    margin-right: 0;
}
.social-box__item:hover {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.6);
}
.social-box__item_tw:hover {
    background: #55acee;
}
.social-box__item_gp:hover {
    background: #dd4b39;
}
.social-box__item_fb:hover {
    background: #3b5999;
}
.social-box__item_li:hover {
    background: #0077B5;
}
.social-box__item_yt:hover {
    background: #cd201f;
}
.social-box__item::after,
.social-box__item_in::before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    transition: all 0.2s ease;
}
.social-box__item::after {
    opacity: 0.2;
}
.social-box__item_in::before {
    background: linear-gradient(-45deg, #feda75, #fa7e1e, #d62976, #962fbf, #4f5bd5);
    opacity: 0;
}
.social-box__item_tw::after {
    background: url(../../images/footer/footer-icon-twitter.png) no-repeat;
    background-position: center center;
    background-size: 18px 18px;
}
.social-box__item_gp::after {
    background: url(../../images/footer/footer-icon-google-plus.png) no-repeat;
    background-position: center center;
    background-size: 18px 18px;
}
.social-box__item_fb::after {
    background: url(../../images/footer/footer-icon-facebook.png) no-repeat;
    background-position: center center;
    background-size: 18px 18px;
}
.social-box__item_li::after {
    background: url(../../images/footer/footer-icon-linkedin.png) no-repeat;
    background-position: center center;
    background-size: 18px 18px;
}
.social-box__item_yt::after {
    background: url(../../images/footer/footer-icon-youtube.png) no-repeat;
    background-position: center center;
    background-size: 18px 18px;
}
.social-box__item_in::after {
    background: url(../../images/footer/footer-icon-instagram.png) no-repeat;
    background-position: center center;
    background-size: 18px 18px;
}
.social-box__item:hover::after {
    opacity: 1;
}
.social-box__item_in:hover::before {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.6);
    opacity: 1;
}
.about-finance {
    width: 100%;
    width: 260px;
    color: #f1f1f1;
}
.about-finance__title {
    margin-bottom: 40px;
    font-size: 16px;
}
.about-finance__item {
    display: inline-block;
    width: 100%;
    margin-bottom: 24px;
    position: relative;

    color: #828181;
    line-height: 20px;

    transition: all 0.2s ease;
}
.about-finance__item:not(:last-child)::after {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    position: absolute;
    left: 0;
    bottom: -14px;

    background-color: #3c3c3c;
}
.about-finance__link {
    display: inline-block;
    padding-left: 22px;

    color: #828181;
    text-decoration: none;

    transition: all 0.2s ease;
}
.about-finance__link:hover {
    color: #f1f1f1;
}
.about-finance__link_mail::before,
.about-finance__link_phone::before,
.about-finance__link_address::before {
    content: '';
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    left: 0;
    top: 3px;

    opacity: 0.2;
    transition: all 0.2s ease;
}
.about-finance__link_mail::before {
    background: url(../../images/footer/footer-icon-mail.png) no-repeat;
    background-position: center center;
    background-size: 14px 14px;
}
.about-finance__link_phone::before {
    background: url(../../images/footer/footer-icon-phone.png) no-repeat;
    background-position: center center;
    background-size: 14px 14px;
}
.about-finance__link_address::before {
    background: url(../../images/footer/footer-icon-placeholder.png) no-repeat;
    background-position: center center;
    background-size: 14px 14px;
}
.about-finance__link:hover::before {
    opacity: 1;
}
.about-finance__item_phone {
    padding-left: 22px;
}
.about-finance__item_address {
    padding-left: 22px;
}
/* End footer styles */

/* Footer-nav styles */
.footer-nav {
    width: 100%;
    background: #292929;
}
.footer-nav__inner{
    display: flex;
    justify-content: flex-end;
    padding: 25px 0;
}
.nav__link_theme_simple {
    color: #6b6b6b;
}
.nav__link_theme_simple:hover {
    color: #c0c0c0;
}
.nav__link_theme_simple::after {
    display: none;
}
.nav__link_size_sm {
    font-size: 13px;
    text-transform: none;
}
.nav__link_size_sm:not(:last-child) {
    margin-right: 24px;
}
/* End footer-nav styles */